import React from 'react'
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const BreadCrumb = styled(Link)`
    text-decoration: none;
    color: #8B8273;
    font-size: 14px;
    font-weight: 300;
    margin-right: 20px;
    position: relative;
    &::before{
        content: '>';
        position: absolute;
        top: 0px;
        right: -15px;
    }
    &:last-child::before{
        content: none;
    }
`;

const LoignInput = styled.input`
    width: 350px;
    height: 50px;
    padding: 0 10px;
    border: 1px solid #8B8273;
    border-radius: 5px;
    display: block;
    margin: 0 auto 20px;
`;  

const LoginBtn = styled.button`
    width: 370px;
    height: 50px;
    border: none;
    background-color: #243B55;
    border-radius: 5px;
    color: white;
    display: block;
    margin: 0 auto 20px;
    cursor: pointer;
`;

const SignLink = styled(Link)`
    text-decoration: none;
    color: #8B8273;
`;

export default function Login() {
  return (
    <div style={{width: '1200px', margin: '0 auto'}}>
        <div style={{marginTop:'20px'}}>
            <BreadCrumb to='/'></BreadCrumb>
            <BreadCrumb to={'/login'}></BreadCrumb>
        </div>
        <h3 style={{fontSize: '32px', margin: '40px 0 60px', textAlign: 'center'}}></h3>
        <div>
            <LoignInput placeholder='' />
            <LoignInput placeholder='' />
        </div>
        <LoginBtn></LoginBtn>
        <ul style={{listStyle:'none', display: 'flex', justifyContent:'center', fontSize: '14px', color: '#8B8273'}}>
            <li> </li>
            <li style={{margin: '0 20px'}}> </li>
            <li><SignLink to={'/sign'}></SignLink></li>
        </ul>
    </div>
  )
}
